const mutations = {
  watchWinResize (state, clientWidth) {
    let winWidth = clientWidth < 768 ? 'xs' : (clientWidth >= 768 && clientWidth < 991 ? 'sm' : (clientWidth >= 992 && clientWidth < 1200 ? 'md' : 'lg'));

    if (state.winsize !== winWidth) {
      state.winsize = winWidth;
    }
  },

  readingCard(state, isTrue) {
    state.isReadingCard = isTrue;
  }
};

export default mutations;



