import fetch from '@/utils/fetch1';

export function getCitys() {
  return fetch({
    baseURL: 'https://easy-mock.com/mock/59a44c219d8ef6191d07f0af/inwill',
    url: '/citys',
    method: 'get'
  })
}

export function getStoreList() {
  return fetch({
    url: '/api/store/lists',
    method: 'get'
  });
}



