const readCard = {
  state: {
    cardID: undefined,
    deviceNumber: undefined
  },
  mutations: {
    readCard: (state) => {
      state.cardID = window.IDCardReader.getcardid();
    },
    readCardOnce: (state) => {
      state.cardID = window.IDCardReader.getcardidonce();
    },
    readDeviceNumber: (state) => {
      state.deviceNumber = window.IDCardReader.pcdgetdevicenumber();
    },
    beep: () => {
      window.IDCardReader.pcdbeep(100);
    }
  },
  getters: {
    isCanReadCard: (state) => {
      return (typeof window.IDCardReader.getcardid === 'string');
    }
  },
  actions: {
    actionReadCard({ commit, state }) {
      commit('readCard');
      commit('beep');
    },
    actionReadCardOnce({ commit, state }) {
      commit('readCardOnce');
      commit('beep');
    },
    actionReadDeviceNumber({ commit, state }) {
      commit('readDeviceNumber');
      commit('beep');
    }
  }
};

export default readCard;



