export function parseTime(time, cFormat) {
  if (arguments.length === 0) {
    return null;
  }
  const format = cFormat || '{y}-{m}-{d} {h}:{i}:{s}';
  let date;
  if (typeof time === 'object') {
    date = time;
  } else {
    if (('' + time).length === 10) time = parseInt(time) * 1000;
    date = new Date(time);
  }
  const formatObj = {
    y: date.getFullYear(),
    m: date.getMonth() + 1,
    d: date.getDate(),
    h: date.getHours(),
    i: date.getMinutes(),
    s: date.getSeconds(),
    a: date.getDay()
  };
  const time_str = format.replace(/{(y|m|d|h|i|s|a)+}/g, (result, key) => {
    let value = formatObj[key];
    if (key === 'a') return ['一', '二', '三', '四', '五', '六', '日'][value - 1];
    if (result.length > 0 && value < 10) {
      value = '0' + value;
    }
    return value || 0;
  });
  return time_str;
}

export function formatTime(time, option) {
  time = +time * 1000;
  const d = new Date(time);
  const now = Date.now();

  const diff = (now - d) / 1000;

  if (diff < 30) {
    return '刚刚';
  } else if (diff < 3600) { // less 1 hour
    return Math.ceil(diff / 60) + '分钟前';
  } else if (diff < 3600 * 24) {
    return Math.ceil(diff / 3600) + '小时前';
  } else if (diff < 3600 * 24 * 2) {
    return '1天前';
  }
  if (option) {
    return parseTime(time, option);
  } else {
    return d.getMonth() + 1 + '月' + d.getDate() + '日' + d.getHours() + '时' + d.getMinutes() + '分';
  }
}

export function isMobile() {
  const windowObject = function (value) {
    return value != null && typeof value === 'object' && 'setInterval' in value;
  };

  const freeSelf = windowObject(typeof self === 'object' && self) && self;
  const navigator = freeSelf && freeSelf.navigator;
  const appVersion = (navigator && navigator.appVersion || '').toLowerCase();
  const userAgent = (navigator && navigator.userAgent || '').toLowerCase();

  const isIpad = userAgent.match(/ipad.+?os (\d+)/);
  const isIphone = isIpad ? null : userAgent.match(/iphone(?:.+?os (\d+))?/);
  const isIpod = userAgent.match(/ipod.+?os (\d+)/);
  const isAndriod = /android/.test(userAgent) && /mobile/.test(userAgent);
  const isBlackberry = /blackberry/.test(userAgent) || /bb10/.test(userAgent);
  const isWindows = /win/.test(appVersion);
  const isWindowsPhone = isWindows && /phone/.test(userAgent);

  return isIphone || isIpod || isAndriod || isBlackberry || isWindowsPhone;
}

export function formatDateTime(time) {
  if (!time) {
    return '';
  }
  time = new Date(time * 1000);
  const year = time.getFullYear();
  let month = time.getMonth() + 1;
  let day = time.getDate();
  month = month < 10 ? '0' + month : month;
  day = day < 10 ? '0' + day : day;
  return year + '-' + month + '-' + day;
}

// 查找某字符串第n次出现的位置，num从0开始
export function nIndexOf(str, cha, num) {
  var x = str.indexOf(cha);
  for (var i = 0; i < num; i++) {
    x = str.indexOf(cha, x + 1);
  }
  return x;
}

import lrz from '../../node_modules/lrz/dist/lrz.all.bundle';

// 控制图片上传类型和大小
export function imgBeforeUpload(file) {
  const isJpg = file.type === 'image/jpeg',
    isPng = file.type === 'image/png';
  const isLt2M = file.size / 1024 / 1024 < 5;
  if (!isJpg && !isPng) {
    // 类型不正确，返回false
    return new Promise((res, rej) => {
      throw new Error('图片类型必须为jpg、jpeg、png！');
    })
  }
  if (!isLt2M) {
    return new Promise((res, rej) => {
      throw new Error('图片大小不能超过5MB！');
    })
  }
  const lrzOption = {
    fieldName: 'image',
    width: 900
  }

  return lrz(file, lrzOption);
}



