import Vue from 'vue';
import Router from 'vue-router';
const _import = require('./_import_' + process.env.NODE_ENV);
// in development env not use Lazy Loading,because Lazy Loading large page will cause webpack hot update too slow.so only in production use Lazy Loading

/* layout */
import Layout from '../views/layout/Layout';

/* login */
const Login = _import('login/index');

/* dashboard */
const dashboard = _import('dashboard/index');

/* error page */
const Err404 = _import('404');

// 会员模块
const memberList = _import('member/list');
const memberNew = _import('member/new');
const memberInfo = _import('member/info');
const cardEdit = _import('member/cardEdit');

const recvDataSSB = _import('recvData/subscribe');
const recvDataFD = _import('recvData/feedback');
const recvDataJ = _import('recvData/join');
const recvDataO = _import('recvData/orderList');

// 访客管理
const visitorRecord = _import('visitor/record');
const visitorList = _import('visitor/list');

// 会员签到
const sign = _import('sign/index');

// 门店展示
const storeShow = _import('minipage/storeShow');
const storeAlbum = _import('minipage/storeAlbum');

const course = _import('minipage/course');
const carousel = _import('minipage/carousel');

Vue.use(Router);

/**
 * icon : the icon show in the sidebar
 * hidden : if `hidden:true` will not show in the sidebar
 * redirect : if `redirect:noredirect` will not redirct in the levelbar
 * noDropdown : if `noDropdown:true` will not has submenu in the sidebar
 * meta : `{ role: ['admin'] }`  will control the page role
 **/
export const constantRouterMap = [
  { path: '/login', component: Login, hidden: true },
  { path: '/404', component: Err404, hidden: true },
  {
    path: '/',
    component: Layout,
    redirect: '/dashboard',
    name: 'Home',
    hidden: true,
    children: [{ path: 'dashboard', component: dashboard }]
  }
];

export default new Router({
  // mode: 'history', //后端支持可开
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
});

export const asyncRouterMap = [
  {
    path: '/sign',
    component: Layout,
    redirect: '/sign/index',
    name: '',
    icon: 'zujian',
    noDropdown: true,
    children: [{ path: 'index', component: sign, name: '会员签到', meta: { role: ['admin'] }}]
  },

  {
    path: '/member',
    component: Layout,
    redirect: '/member/list',
    name: '会员管理',
    icon: 'zujian',
    children: [
      {
        path: 'list',
        component: memberList,
        name: '会员列表'
      },
      {
        path: 'new',
        component: memberNew,
        name: '添加会员'
      },
      {
        path: ':id/info',
        // path: 'info',
        component: memberInfo,
        name: '会员资料',
        hidden: true
      },
      {
        path: ':id/edit',
        component: memberNew,
        name: '修改会员资料',
        hidden: true
      },
      {
        path: ':id/cardAdd',
        component: cardEdit,
        name: '会员卡续卡',
        hidden: true
      },
      {
        path: ':id/cardStop',
        component: cardEdit,
        name: '会员卡停卡',
        hidden: true
      }
    ]
  },

  {
    path: '/visitor',
    component: Layout,
    redirect: 'noredirect',
    name: '访客管理',
    icon: 'zujian',
    children: [
      {
        path: 'record',
        component: visitorRecord,
        name: '访客登记'
      },
      {
        path: 'list',
        component: visitorList,
        name: '访客列表'
      }
    ]
  },

  {
    path: '/recvData',
    component: Layout,
    redirect: 'noredirect',
    name: '管理数据',
    icon: 'zujian',
    children: [
      {
        path: 'subscribe',
        component: recvDataSSB,
        name: '预约健康'
      },
      {
        path: 'feedback',
        component: recvDataFD,
        name: '意见反馈'
      },
      {
        path: 'join',
        component: recvDataJ,
        name: '加盟人'
      },
      {
        path: 'order',
        component: recvDataO,
        name: '会员订单列表'
      }
    ]
  },

  {
    path: '/minipage',
    component: Layout,
    redirect: 'noredirect',
    name: '微主页',
    icon: 'zujian',
    children: [
      {
        path: 'storeShow',
        component: storeShow,
        name: '门店展示'
      },
      {
        path: 'storeAlbum/:id',
        // path: 'info',
        component: storeAlbum,
        name: '相册详情',
        hidden: true
      },
      {
        path: 'course',
        // path: 'info',
        component: course,
        name: '特色课程'
      },
      {
        path: 'carousel',
        component: carousel,
        name: '首页轮播图'
      }
    ]
  },

  { path: '*', redirect: '/404', hidden: true }
];



