import fetch from '@/utils/fetch1';

export function login(name, password) {
  return fetch({
    // baseURL: 'https://easy-mock.com/mock/59a44c219d8ef6191d07f0af/inwill',
    url: '/api/admin/checkAdminLogin',
    method: 'post',
    data: {
      name,
      password
    }
  });
}

export function getInfo(token) {
  return fetch({
    baseURL: 'https://easy-mock.com/mock/59a44c219d8ef6191d07f0af/inwill',
    url: '/user/info',
    method: 'get',
    params: { token }
  });
}

export function logout() {
  return fetch({
    baseURL: 'https://easy-mock.com/mock/59a44c219d8ef6191d07f0af/inwill',
    url: '/user/logout',
    method: 'post'
  });
}



