import fetch from '@/utils/fetch1';
const qs = require('qs');

export function add(params) {
  return fetch({
    url: '/api/member/add',
    method: 'post',
    data: qs.stringify(params),
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  });
}

export function memberInfo(params) {
  return fetch({
    url: '/api/member/info',
    method: 'POST',
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    data: qs.stringify(params)
  });
}

export function lists(params) {
  return fetch({
    url: '/api/member/lists',
    method: 'post',
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    data: qs.stringify(params)
  });
}

export function upload(params) {
  return fetch({
    url: '/api/upload/image',
    method: 'post',
    headers: { 'Content-Type': 'multipart/form-data' },
    data: params
  });
}

export function uploadBase64(params) {
  return fetch({
    url: '/api/upload/image',
    method: 'post',
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    data: qs.stringify(params)
  });
}

