const getters = {
  sidebar: state => state.app.sidebar,
  token: state => state.user.token,
  avatar: state => state.user.avatar,
  name: state => state.user.name,
  roles: state => state.user.roles,
  permission_routers: state => state.permission.routers,
  addRouters: state => state.permission.addRouters,
  cardID: state => state.readCard.cardID,
  deviceNumber: state => state.readCard.deviceNumber,
  memberInfo: state => state.member.memberInfo,
  isGettingInfo: state => state.member.isGettingInfo,
  storeList: state => state.app.storeList
};
export default getters;



