import { memberInfo } from '@/api/member';

const member = {
    namespaced: true,
    state: {
        // 单个会员的信息
        memberInfo: null,
        isGettingInfo: false
    },
    mutations: {
        // 为单个会员赋值
        setInfo(state, infoData) {
            state.memberInfo = infoData;
        }
    },
    actions: {
        getInfo({ commit, state }, params) {
            // if (state.memberInfo && state.memberInfo.id === params.id) {
            //     return;
            // }
            state.isGettingInfo = true;
            // 模拟延迟加载
            // setTimeout(function () {
            //     memberInfo(params).then(response => {
            //         const memberData = response.data;
            //         commit('setInfo', memberData);
            //         state.isGettingInfo = false;
            //     })
            // }, 5000);

            memberInfo(params).then(response => {
                const memberData = response.data;
                commit('setInfo', memberData);
                state.isGettingInfo = false;
            })

        }
    }
}

export default member;


